.feed {
  padding: 2px; }
  .feed .ht {
    color: #228be6;
    font-weight: 600; }
  .feed .list {
    box-sizing: border-box;
    padding-top: 57px;
    width: 100%;
    height: calc(100vh - 84px);
    overflow-y: scroll;
    overflow-x: hidden; }
    @media (max-width: 679px) {
      .feed .list {
        padding-top: 0; } }

.loading {
  height: calc(100vh - 80px); }

.following-rec {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }
  .following-rec .each-rec {
    cursor: pointer;
    margin: 20px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    background: #dee2e6;
    min-width: 200px;
    border-radius: 20%;
    align-items: center; }
    .following-rec .each-rec:hover {
      background: #ced4da;
      -webkit-transition: 0.3s;
      transition: 0.3s; }
    .following-rec .each-rec .profile-pic {
      background: #adb5bd;
      width: 100px;
      height: 100px;
      border-radius: 50%; }
      .following-rec .each-rec .profile-pic img {
        object-fit: contain;
        border-radius: 50%; }

.searched-hashtag {
  font-size: 1.8rem;
  font-weight: 500;
  color: #495057;
  margin-top: 15px;
  margin-bottom: 15px; }
