.login {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: calc(100vh - 120px);
  flex-wrap: wrap; }
  .login .inner {
    background: white;
    width: 340px;
    height: 600px;
    box-shadow: -60px 0px 100px -90px #000000, 60px 0px 100px -90px #000000;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 3.5%; }
    @media (max-width: 1023px) {
