        width: 100%;
        margin-top: 65px; } }
  .login input {
    margin-bottom: 5px;
    text-align: center;
    width: 290px;
    height: 30px;
    outline: none;
    border: none;
    border-bottom: 1px solid #343a40;
    font-size: 1.5rem; }
  .login .submit {
    width: 290px;
