.modal {
  background: rgba(0, 0, 0, 0.25);
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }
  .modal .content {
    background: white;
    padding: 3rem;
    width: 200px;
    min-width: 200px;
    height: auto;
    font-size: 1.2rem; }
    .modal .content .flist {
      max-height: 300px;
      overflow: auto; }
    .modal .content .close {
      -webkit-transition: 0.3s;
      transition: 0.3s;
      padding: 3px;
      background: #228be6;
      cursor: pointer;
      font-size: 1rem;
      color: #fff;
      width: 70px;
      border-radius: 5%;
      border: white; }
      .modal .content .close:hover {
        background-color: #1971c2; }

.show-profile {
  color: #228be6;
  cursor: pointer; }
  .show-profile:hover {
    color: #1864ab; }
